.Resultados__list {
  display: flex;
  flex-direction: column; }

.Resultados__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #9D8855;
  padding: 17px 0;
  width: 100%; }

@media screen and (max-width: 768px) {
  .Resultados__item {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start; } }

.Resultados__item-resultado {
  font-weight: 500;
  font-size: 17px; }

@media screen and (max-width: 768px) {
  .Resultados__item-resultado {
    margin-top: 15px;
    padding-left: 33px; } }

.Resultados__item-nomes {
  display: flex;
  flex-direction: column;
  color: #484848;
  font-weight: lighter;
  font-size: 17px; }

.Resultados__item-nomes .cavaleiro {
  position: relative;
  display: block;
  padding-left: 34px;
  margin-bottom: 7px; }

.Resultados__item-nomes .cavaleiro:after {
  content: '';
  position: absolute;
  top: 0;
  height: 100%;
  width: 27px;
  left: 0;
  background-image: url(../../../assets/images/ico/cavaleiro.svg);
  background-position: left center;
  background-size: contain;
  background-repeat: no-repeat; }

.Resultados__item-nomes .cavalo {
  position: relative;
  display: block;
  padding-left: 34px; }

.Resultados__item-nomes .cavalo:after {
  content: '';
  position: absolute;
  top: 0;
  height: 100%;
  width: 27px;
  left: 0;
  background-image: url(../../../assets/images/ico/cavalo.svg);
  background-position: left center;
  background-size: contain;
  background-repeat: no-repeat; }
